import request from '@/utils/request'

export function getInfo(){
  return request({
    url:'/manager/shopInformation',
    method:'get',
  })
}

export function updateInfo(data){
  return request({
    url:'/manager/updateShop',
    method:'post',
    data:data
  })
}


export function qrCode(deskNum){
  return request({
    url:'/manager/getQRcode?deskNum='+deskNum,
    method:'post'
  })
}



