<template>
  <div class="center">
      <el-card class="box-card">
        <el-form :model="value" :rules="rules" label-width="120px" style="width: 600px" size="small">
          <el-form-item label="店铺名称：" prop="name">
            <el-input v-model="value.name"/>
          </el-form-item>
          <el-form-item label="店铺公告：" prop="notice">
            <el-input v-model="value.notice"/>
          </el-form-item>
          <el-form-item label="首页地址：" prop="url">
            <el-input v-model="value.url"/>
          </el-form-item>
          <el-form-item label="店铺图片：" prop="picture">
            <el-upload
              class="avatar-uploader"
              :action=url
              name="file"
              :on-success="handleUpload"
              :show-file-list="false">
              <img v-if="value.image" :src="value.image" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item style="text-align: center">
          <el-button type="primary" size="medium" @click="commit()">提交</el-button>
          </el-form-item>

        </el-form>
      </el-card>
  </div>

</template>

<script>
  import {getInfo, updateInfo} from "../../api/shop";


  export default {
    data() {
      return {
        value: {
          id: '',
          name: '',
          image: '',
          url: '',
          notice: ''
        },
        url: 'http://localhost:8088/manager/uploadShopImg',
        //选中商品分类的值
        rules: {
          name: [
            {required: true, message: '请输入店铺名称', trigger: 'blur'},
            {min: 2, max: 140, message: '长度在 2 到 140 个字符', trigger: 'blur'}
          ],
          url: [{required: true, message: '请输入店铺URL', trigger: 'blur'}],
          notice: [{required: false, message: '请输入店铺公告', trigger: 'blur'}],

          picture: [{required: true, message: '请选择图片', trigger: 'blur'}],
        },

      };
    },
    created() {
      this.getShopInfo();
    },
    methods: {
      //处理编辑逻辑
      getShopInfo() {
        getInfo().then(response => {
          this.value = response.data;
        });
      },

      handleUpload(response, file, fileList) {
        this.value.image = response.data
      },

      commit(formName) {
        updateInfo(this.value).then(response => {
          this.$message({
            type: 'success',
            message: '提交成功',
            duration: 1000
          });
        })
      }
    }
  }
</script>

<style scoped>


  .center {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 20px;
    width: 100%;
  }

  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



